.mainStoryCard,
.storyCard {
  height: 95%;
  overflow: hidden;
  min-width: 103px;
  width: 103px;
  border-radius: 10px;

  text-align: center;
  font-weight: 600;
  position: relative;

  img {
    height: 100%;
